<script>
import ImageGallery from "../widgets/ImageGallery.svelte"
import RoundedRect from "../primitives/RoundedRect.svelte"

export let markerVisible

let index = 0;

const imageParams = {
    position: {
        x: 0,
        y:0,
        z:0,
    },
    rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
    size: {
        height: 1.5,
        width: 2
    },
	src: ["./static/images/shoe_1.png","./static/images/chicken_salad.png"],
	link: ["https://google.com","https://google.com"],
}

const titleBlock = {
	position:{
		x: imageParams.position.x - imageParams.size.width/2,
		y: imageParams.position.y ,
		z: imageParams.position.z - imageParams.size.height/2,
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	size:{
		height:imageParams.size.height/3,
		width: imageParams.size.width	
	},
	borderRadius: {
		topLeft: 0.05,
		topRight:0.05,
		bottomLeft:0,
		bottomRight:0
	},
	color: "#2B2B2B"
}

const descriptionBlock = {
	size:{
		height:imageParams.size.height,
		width: imageParams.size.width	
	},
	position:{
		x: imageParams.position.x - imageParams.size.width/2,
		y: imageParams.position.y,
		get z(){
			return imageParams.position.z + imageParams.size.height + descriptionBlock.size.height/2
		},
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	
	borderRadius: {
		topLeft: 0,
		topRight:0,
		bottomLeft:0.05,
		bottomRight:0.05
	},
	color: "#2B2B2B"
}

const titleText = {
	text: ["Awesome Sneakers","Jerk Chicken Salad"],
	padding: 0.1,
	position:{
		x: titleBlock.position.x,
		y: titleBlock.position.y + 0.05,
		z: titleBlock.position.z - titleBlock.size.height/2,
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	size:{
		height: titleBlock.size.height ,
		width: titleBlock.size.width - 0.2
	},
	font: "https://cdn.aframe.io/fonts/Roboto-msdf.json",
	color: "white",
	wrapCount: "20", //controls how big the text is
	xOffset: 0.1,
	zOffset: 0.001,
	height: 1,
	align: 'center',
	baseline: 'center',
	letterSpacing: 2,
	anchor: 'left'

}

const descriptionText = {
	text: "Get that best-ever feeling on every run. These neutral shoes have a stretchy knit upper with ventilation in key sweat zones to help you stay cool. Energy-returning cushioning and a flexible outsole work together to give you a smooth ride from touch-down to toe-off.",
	padding: 0.1,
	position:{
		x: descriptionBlock.position.x,
		y: descriptionBlock.position.y + 0.05,
		z: descriptionBlock.position.z  - descriptionBlock.size.height/2,
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	size:{
		height: descriptionBlock.size.height ,
		width: descriptionBlock.size.width - 0.2 
	},
	font: "https://cdn.aframe.io/fonts/Roboto-msdf.json",
	color: "white",
	wrapCount: "30", //controls how big the text is
	xOffset: 0.1,
	zOffset: 0.001,
	height: 1,
	align: 'left',
	baseline: 'center',
	letterSpacing: 2,

}

function nextImage(method){
    let totalImages = imageParams.src.length;
    if(method === "next") {
        index = index + 1;
        if(index > totalImages){
            index = 0;
        }
    } else if (method === 'prev') {
        index = index - 1;
        if(index < 0){
            index = totalImages-1;
        }
    }  

}

function redirectTo(){
	window.location.href = imageParams.link[index]
}

AFRAME.registerComponent('gallery-controls',{
    init:function(){
		const prev = document.getElementById('prev');
		const next = document.getElementById('next');
		this.aimage = document.getElementById('image-gallery');

		const imagesArray = imageParams.src;
		this.imageLength = imagesArray.length;
		this.index = 0;
		

		prev.addEventListener('click', function(){
			this.nextImage('prev')
		});
		next.addEventListener('click', function(){
			this.nextImage("next")
		});
        //arrow.addEventListener('click', this.handleClick)
	},

    handleClick: function(ev) {
        ev.stopPropagation();
        ev.preventDefault();
		const method = ev.target.getAttribute('id')
        const el = ev.detail.intersection && ev.detail.intersection.object.el;

        if (el && el === ev.target) {
            this.nextImage(method)
            ev.target.setAttribute('src', imageParams.src[index])
        }
	},
	nextImage: function(method){

		if(method === "next") {
			this.index = this.index + 1;
			if(this.index > this.imageLength -1){
				this.index = 0;
			}
		} else if (method === 'prev') {
			this.index = this.index - 1;
			if(this.index < 0){
				this.index = this.imageLength-1;
			}
		}

		this.aimage.setAttribute('src', imageParams.src[this.index]) 
	},
})


</script>

<RoundedRect params={titleBlock}/>
<a-text value={titleText.text[index]} position={`${titleText.position.x} ${titleText.position.y} ${titleText.position.z}`} rotation={`${titleText.rotation.x} ${titleText.rotation.y} ${titleText.rotation.z}`} width={titleText.size.width} height={titleText.size.height} color={titleText.color} wrap-count={titleText.wrapCount} z-offset={titleText.zOffset} x-offset={titleText.xOffset} letter-spacing={titleText.letterSpacing} font={titleText.font} align={titleText.align} anchor="left"></a-text>
<a-image gallery-controls id="image-gallery" position={`${imageParams.position.x} ${imageParams.position.y} ${imageParams.position.z}`} rotation={`${imageParams.rotation.x} ${imageParams.rotation.y} ${imageParams.rotation.z}`} height={imageParams.size.height} width={imageParams.size.width} src={imageParams.src[index]}></a-image>
<RoundedRect params={descriptionBlock} />
<a-text value={descriptionText.text} position={`${descriptionText.position.x} ${descriptionText.position.y} ${descriptionText.position.z}`} rotation={`${descriptionText.rotation.x} ${descriptionText.rotation.y} ${descriptionText.rotation.z}`} width={descriptionText.size.width} height={descriptionText.size.height} color={descriptionText.color} wrap-count={descriptionText.wrapCount} z-offset={descriptionText.zOffset} x-offset={descriptionText.xOffset} letter-spacing={descriptionText.letterSpacing} font={descriptionText.font} anchor="left"></a-text>


<img id="prev" src="./static/icons/left_button.svg" alt="left button"/>
<img id="next" src="./static/icons/right_button.svg" alt="right button"/> 

{#if markerVisible}
<section id="cta-section">
	<button on:click={redirectTo}>Buy Product</button>
</section>
{/if}

<style>
#prev {
	position:fixed;
	left: 5%;
	top: 50%;
	width: 5vw;
	height: auto;
	transform: translateY(-50%);
	z-index: 2;
}

#next {
	position:fixed;
	right: 5%;
	top: 50%;
	width: 5vw;
	height: auto;
	transform: translateY(-50%);
	z-index: 2;
}

#cta-section {
	position: fixed;
	display: flex;
	align-items: center;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 15%;
	background-color: white;
	border-top-left-radius: 0.5rem;
	border-top-right-radius: 0.5rem;
	z-index: 2;
}

#cta-section button {
	border: none;
	border-radius: 0.25rem;
	background-color: #3900DC;
	padding: 0.5rem;
	color: white;
	width: 80%;
	margin: 0 auto;
}
</style>